* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}
body,
html {
  width: 100dvw;
  height: 100dvh;
  overflow: hidden;
}
.app-whole-wrapper {
  width: 100dvw;
  height: 100dvh;
  display: grid;
  place-items: center;
  background: rgb(28, 28, 37);
}
.app-wrapper {
  max-width: 60dvw;
  max-height: 80dvh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
/* Library */
.Library-header {
  display: flex;
  align-items: center;
  min-width: 100dvw;
  justify-content: space-around;

  h2 {
    cursor: pointer;
  }
}
.songs-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
}

.song-image {
  width: 30dvw;
  border-radius: 50%;
  overflow: hidden;
  img {
    width: 100%;
  }
}
.song-text,
.Library-header {
  color: rgb(150, 148, 151);
}

/* Style the input Slider skip forward or backward */
.timeline-wrapper {
  display: flex;
  max-width: 80dvw;
  align-items: center;
  justify-content: center;
  color: #fff;
  box-sizing: border-box;
}


.timeline-slider {
  position: relative;
  margin: 0dvw 1dvw;
  overflow: hidden;
}
/* slider wrapper */
.slider{
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
  width: 60dvw;
  height: 1rem;
margin-inline: 2dvw;

}
/* Remove the Default Styles from the Input Slider */
.timeline-wrapper input[type="range"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  height: 1rem;
  width: 100%;
  cursor: pointer;
  border: transparent;
 background: #00000049;

}
.timeline-wrapper input[type="range"]::-webkit-slider-thumb,.timeline-wrapper input[type="range"]::-moz-range-thumb  {
  border: none;
  display: none; 
  background-color: transparent;
}

/* Video current status Duration UI */
.timeline-style {
  transform: translateX(-100%);
  position: absolute;
  top: 0;
  left: 0;
  width: 101%;
  height: 100%;
  border-radius: 1rem;
  border: none;
  background: linear-gradient(to left ,rgba(9, 73, 119, 0.445) , rgba(161, 23, 72, 0.776));
  pointer-events: none;
}


.timeline-btns-wrapper {
  position: relative;
  max-width: 100dvw;
  padding: 1dvw;
  display: flex;
  align-items: center;
  justify-content: center;
  > button {
    color: rgb(150, 148, 151);
    font-weight: 400;
    background: transparent;
    padding: 0.5rem 0.6rem 0.3rem 0.6rem;
    margin-inline: 0.3rem;
    font-weight: 400;
    cursor: pointer;
    transition: all 200ms;
    outline: none;
    border: 0.2em ridge rgb(150, 148, 151);
    background: transparent;
    > svg {
      font-size: 1.2rem;
    }
  }
  > button:first-child {
    border-right: none;
    border-radius: 50% 0% 0% 50%;
    > svg {
      font-size: 1.5rem;
    }
  }
  > button:nth-child(2) {
    border-right: 0.001em ridge rgb(150, 148, 151);
    border-left: 0.001em ridge rgb(150, 148, 151);
    border-radius: 50% 0% 50% 0%;
    padding: 0.6rem 1rem 0.4rem 1rem;
  }
  > button:nth-child(3) {
    border-left: none;
    border-radius: 0% 50% 50% 0%;
    > svg {
      font-size: 1.5rem;
    }
  }
  button:hover {
    border-color: #fff;
    color: #fff;
  }
}
.volume-icon-wrapper {
  display: flex;
  align-items: center;
  position: absolute;
  right: 0;
  svg {
    font-size: 2rem;
  }
  input {
    height: 0.2rem;
    width: 100px;
    cursor: pointer;
    background: #8c8c8d;
  }
  input[type="range"]::-moz-range-thumb {
    background: rgb(106, 106, 108);
    border-radius: 100%;
    font-size: 1rem;
    border: none;
  }
}
/* Song List or Library */

.song-list-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  min-width: 30dvw;
  height: 100dvh;
  overflow-y: scroll;
  background: #ffffff2c;
  transition: transform 200ms;
  backdrop-filter: blur(5px);
  box-sizing: border-box;
  scrollbar-width: thin;
  scrollbar-color: #807d7d4a transparent;
  scroll-behavior: smooth;
  scrollbar-gutter: stable both-edges;
}


.active-song-list-wrapper-hidden {
  top: 0;
  left: 0;
  transform: translateX(-100%) !important;
}
.active-song-list-wrapper-visible {
  transform: translateX(0%) !important;
  z-index: 1;
}

.song-library-list-wrapper {
  display: flex;
  padding: 2dvw;
  align-items: center;
  cursor: pointer;
  transition: all 200ms;
  color: #cac2c2da;
  img {
    width: 5dvw;
    border-radius: 50%;
    pointer-events: none;
  }
  p::selection {
    pointer-events: none;
  }
  &:hover {
    background: #ffffff52;
    color: #fff;
    img {
      scale: 1.1;
    }
  }
}

#toggleLibBtn {
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  font-weight: 200;
  z-index: 5;
  padding: 0.6rem 0.4rem;
  border: 2px ridge rgb(150, 148, 151);
  transition: all 200ms;
border-radius: 1rem 0rem 1rem 0rem;
  >*{
    margin: 0rem 0.2rem;
    transition: all 200ms;
    color:rgb(150, 148, 151);
  }
}
#toggleLibBtn:hover {
  border: 2px solid rgb(211, 208, 212);
  >*{
    color: rgb(197, 195, 198);
  }
}
.active-song {
  background: #00000092 !important;
  color: #fff;
}

/*ðŸ“±ðŸ“²ðŸ“² For Mobile Devices  and Tablet Display*/
@media screen and (min-width: 200px) and (max-width: 768px) and (max-height:700px) {
  .app-whole-wrapper {
    align-content: flex-end;
    justify-items: center;
    max-height: 100dvh;
  }
  .Library-header {
    position: absolute;
    top: 0;
    left: 0;
    padding-top: 8dvh;
  }
  .app-wrapper {
    display: flex;
    flex-direction: column;
    width: 100dvw;
    height: fit-content;
    align-items: center;
    justify-content: start;
  }
  .song-list-wrapper {
    width: 100dvw;
    backdrop-filter: blur(10px);
    > * {
      padding: 7dvw;
      color: #000;
      background: #dadada7a;
    }
  }
  .songs-details {
    margin-bottom: 10dvh;
    > .song-image {
      width: 60dvw;
    }
  }
  .timeline-wrapper {
    min-width: 100dvw !important;
    justify-content: center;
    align-items: center;
    > input {
      width: 65dvw;
    }
  }
  #toggleLibBtn {
    transform: translateY(-5dvh);
    font-size: 1.5rem;
    z-index: 5;
    font-weight: 100;
    color: #fff;
  }
.song-list-wrapper{
  height: calc(93dvh - 1dvh);
}
  .active-song {
    color: #fff;
  }
  .timeline-btns-wrapper {
    z-index: 5;
    width: 100dvw;
    background: #00000092;
    justify-content: flex-start;
    > button {
      border: none;
      padding: 0;
      margin: 0.2rem;
      svg {
        font-size: 0.8rem;
        color: #fff;
      }
    }
    > button:nth-child(2) {
      border: none;
    }
  }
  .volume-icon-wrapper {
    width: 100px;
    right: 3dvw;
    svg {
      font-size: 1.4rem;
    }
    input {
      width: 70px;
    }
  }
}
/* To remove the Hover effects when on mobile devices */
@media (hover: none) {
  #toggleLibBtn {
    /* transform:translateY(-12dvh) ; */
    font-size: 1.5rem;
    z-index: +1;
    font-weight: 100;
    color: #fff;
  }
}

/* For Small Mobile Landscape View */
@media (orientation: landscape) and (min-height: 200px) and (max-height: 600px) {
  .Library-header {
    h1 {
      font-size: 1rem;
    }
    h2{
      z-index: +5;
    }
  }
  .songs-details {
    margin: 0;
  }
  .song-image img {
    width: 30%;
    border-radius: 100%;
  }
  .song-text {
    font-size: 0.5rem;
  }
  .timeline-wrapper {
    justify-content: space-evenly;
  }
  #toggleLibBtn {
    transform: translate(0%, 0%);
    z-index: 5;
  }
  .song-library-list-wrapper {
    padding: 0.4rem;
  }
  .song-list-wrapper {
    max-height: calc(100dvh - 1dvw - 40px) !important;
    max-width: 50dvw;
    overflow-y: scroll;
  }
}
